import geocoderLib from 'node-geocoder';
import httpsAdapter from 'node-geocoder/lib/httpadapter/httpsadapter';
import http from 'http';
import config from '../config';

let geocoderProvider = "google";
let extra = config.google.geocoder;

let geocoder = geocoderLib(geocoderProvider, httpsAdapter(http), extra);

export default geocoder;
